import React from 'react';
import { Route, Routes } from 'react-router-dom';

const BuyCrypto = React.lazy(() => import('./buyCrypto/BuyAmount/BuyAmount'));
const BuyCryptoPreview = React.lazy(() => import('./buyCrypto/ExecuteBuy/ExecuteBuy'));
const BuyCryptoSuccess = React.lazy(() => import('./buyCrypto/BuySuccess/BuySuccess'));

const SellCrypto = React.lazy(() => import('./sellCrypto/SellAmount/SellAmount'));
const SellCryptoPreview = React.lazy(() => import('./sellCrypto/ExecuteSell/ExecuteSell'));
const SellCryptoSuccess = React.lazy(() => import('./sellCrypto/SellSuccess/SellSuccess'));

const ReceiveCrypto = React.lazy(() => import('./receiveCrypto/WalletName/ReceiveWalletName'));

const SendCrypto = React.lazy(() => import('./sendCrypto/SendCryptoAmount/SendCryptoAmount'));
const SendCryptoPreview = React.lazy(() => import('./sendCrypto/ExecuteSend/ExecuteSend'));
const SendCryptoSuccess = React.lazy(() => import('./sendCrypto/SendCryptoSuccess/SendCryptoSuccess'));

const CryptoRouter = () => (
  <Routes>
    <Route
      path="buy"
      element={(<BuyCrypto />)}
    />
    <Route
      path="buy/preview"
      element={(<BuyCryptoPreview />)}
    />
    <Route
      path="buy/success"
      element={(<BuyCryptoSuccess />)}
    />

    <Route
      path="sell"
      element={(<SellCrypto />)}
    />
    <Route
      path="sell/preview"
      element={(<SellCryptoPreview />)}
    />
    <Route
      path="sell/success"
      element={(<SellCryptoSuccess />)}
    />

    <Route
      path="receive"
      element={(<ReceiveCrypto />)}
    />

    <Route
      path="send"
      element={(<SendCrypto />)}
    />
    <Route
      path="send/preview"
      element={(<SendCryptoPreview />)}
    />
    <Route
      path="send/success"
      element={(<SendCryptoSuccess />)}
    />
  </Routes>
);

export default CryptoRouter;
